<template>
  Whoever has the most tiles gets treated a meal with Shawn <br>
  The first 3 people to get 3 rows/cols/diags get a free drink
  <div class="bingo-board">
    <div 
      class="bingo-tile" 
      v-for="(tile, index) in tiles" 
      :key="index" 
      :class="{ crossed: tile.crossed }"
      @click="crossTile(index)"
    >
      <span>{{ tile.phrase }}</span>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tiles: [
        { phrase: 'Ask someone how they know Shawn', crossed: false },
        { phrase: 'Introduce yourself to someone you did not know', crossed: false },
        { phrase: 'Find a non CS/SWE person and ask what they study/do', crossed: false },
        { phrase: 'Shake hand with a left-handed person', crossed: false },
        { phrase: 'Fistbump with someone who is not a US citizen', crossed: false },
        { phrase: 'Talk 1 min about why you are a cat/dog person', crossed: false },
        { phrase: 'Order a drink', crossed: false },
        { phrase: 'Secret handshake with someone who has the same birth month', crossed: false },
        { phrase: 'Sing a full scale with someone', crossed: false },
        { phrase: 'Finish today`s Wordle', crossed: false },
        { phrase: 'Describe 1 min about your last travel', crossed: false },
        { phrase: 'Describe 1 min about your next travel plan', crossed: false },
        { phrase: 'Talk 1 min about your parents', crossed: false },
        { phrase: 'Find someone who wears glasses and give them a compliment', crossed: false },
        { phrase: 'Learn how to say Happy Birthday from someone bilingual', crossed: false },
        { phrase: 'Chat about your first impression of Shawn', crossed: false },
        { phrase: 'Cross if your daily phone screen time is above 5h this week', crossed: false },
        { phrase: 'Talk 1 min about your most often nightmare', crossed: false },
        { phrase: 'Take a selfie with someone wearing the same color', crossed: false },
        { phrase: 'Discuss 1 min about your fav superpower', crossed: false },
        { phrase: 'Take a selfie with Shawn and send it to him', crossed: false },
        { phrase: 'Describe the city you live in', crossed: false },
        { phrase: 'Talk 1 min about your passion/dream', crossed: false },
        { phrase: 'Cross if you are a nice person <3', crossed: false },
        { phrase: 'Talk 1 min what you do to make the world a better place', crossed: false },
      ]
    };
  },
  mounted() {
    this.loadState();
    this.shuffleTiles();
  },
  methods: {
    crossTile(index) {
      this.tiles[index].crossed = !this.tiles[index].crossed;
      this.saveState(); // Save state whenever a tile is crossed/uncrossed
    },
    shuffleTiles() {
      const storedState = localStorage.getItem(this.storageKey);

      if (!storedState) {
        for (let i = this.tiles.length - 1; i > 0; i--) {
          const j = Math.floor(Math.random() * (i + 1));
          [this.tiles[i], this.tiles[j]] = [this.tiles[j], this.tiles[i]];
        }
        this.saveState(); // Save the shuffled state
      }
    },
    saveState() {
      localStorage.setItem(this.storageKey, JSON.stringify(this.tiles));
    },
    loadState() {
      const storedState = localStorage.getItem(this.storageKey);
      if (storedState) {
        this.tiles = JSON.parse(storedState);
      }
    }
  }
};
</script>

